/* App.css */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Desktop adjustments */
@media (min-width: 768px) {
  .main-container {
    max-width: 800px;
    margin: 0 auto;
    height: 85vh;
  }

  .chat-widget {
    width: 600px;
    max-width: 600px;
    height: 80%;
    bottom: 100px;
    margin: 0 auto;
    padding: 20px;
  }

  .chat-bar {
    max-width: 600px;
    margin: 20px auto;
    padding: 10px 15px;
  }

  .disclaimer {
    position: relative;
    bottom: -30px;
    margin-top: 20px;
    text-align: center;
  }

  button,
  input {
    font-size: 1rem;
  }
}

/* Mobile-specific styles remain unchanged */
@media (max-width: 767px) {
  .main-container {
    max-width: 95%;
    height: 100vh;
    padding: 0;
  }

  .chat-bar {
    max-width: 100%;
    margin-bottom: 4cm;
    padding: 10px;
  }
}

/* Additional animations and styling */
.voiceInputBorder {
  border-width: 0px;
  border-style: solid;
  border-image-source: linear-gradient(180deg, #9aed66 0%, #e9e9eb 50%, #bcd8fa 75%, #c736d9 100%);
}

.shape-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  position: relative;
  width: 100%;
  height: 240px;
}

.shape {
  position: absolute;
}

.circle {
  border-radius: 50%;
  position: absolute;
  animation: breathe 3s infinite;
}

.circle-listening {
  animation: heartbeat 1s infinite;
}

.breathing {
  animation: breathe 3s infinite;
}

.heartbeat {
  animation: heartbeat 1s infinite;
}

@keyframes breathe {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  25%, 75% {
    transform: scale(1.1);
  }
}